.page {
  scroll-behavior: smooth;
  color: #2b2925;
  font-family: Cormorant Infant, Georgia, Times New Roman, serif;
  position: relative;
  overflow-x: hidden;
}

.page__body {
  background-image: url("background-mobile.d4186673.jpg");
  background-position: -390px 100vh;
}

@media (width >= 1024px) {
  .page__body {
    background-image: url("background-desktop.770e65d3.jpg");
    background-position: 0 100vh;
  }
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .4s;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu--opened {
  transform: translateX(0);
}

.page__astro-help {
  margin-bottom: 16px;
}

@media (width >= 1024px) {
  .page__astro-help {
    margin-bottom: 120px;
  }
}

.page__nav {
  position: absolute;
  top: 0;
  right: 0;
}

.page::-webkit-scrollbar {
  width: 0;
}

.page::-webkit-scrollbar-thumb {
  background-color: #0000;
}

.page::-webkit-scrollbar-button:start:decrement {
  display: none;
}

.page::-webkit-scrollbar-button:end:increment {
  display: none;
}

.page::-webkit-scrollbar-track {
  background-color: #0000;
}

