.nav {
  visibility: hidden;
  display: none;
}

@media (width >= 1024px) {
  .nav {
    visibility: visible;
    display: block;
  }
}

.nav__list {
  letter-spacing: .08em;
  text-align: left;
  justify-content: center;
  align-items: center;
  gap: 40px;
  height: 87px;
  font-family: Cormorant Infant;
  font-size: 18px;
  font-weight: 500;
  line-height: 22px;
  list-style-type: none;
  display: flex;
}

.nav__link {
  border-bottom: 1px solid #0000;
  padding-bottom: 5px;
  text-decoration: none;
  transition: all .4s;
}

.nav__link:hover {
  font-family: Cormorant Infant;
  font-weight: 700;
}

.nav__item {
  transition: all .4s;
}

.nav__item:hover {
  transform: scale(1.2);
}

.burger-menu {
  cursor: pointer;
  z-index: 2;
  flex-direction: column;
  width: 40px;
  height: 40px;
  padding: 8px;
  display: flex;
  position: relative;
}

@media (width >= 1024px) {
  .burger-menu {
    visibility: hidden;
    display: none;
  }
}

.burger-menu__opener {
  opacity: 0;
  z-index: 3;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0;
  right: 0;
}

.burger-menu__opener[type="checkbox"]:checked ~ span:first-of-type {
  opacity: 0;
  transition: all .4s;
}

.burger-menu__opener[type="checkbox"]:checked ~ span:nth-of-type(2), .burger-menu__opener[type="checkbox"]:not(:checked) ~ span:first-of-type {
  opacity: 1;
  transition: all .4s;
}

.burger-menu__opener[type="checkbox"]:not(:checked) ~ span:nth-of-type(2) {
  opacity: 0;
  transition: all .4s;
}

.burger-menu__cross {
  opacity: 0;
  background-image: url("cross.7cb1d667.svg");
  background-position: center;
  width: 24px;
  height: 24px;
  position: absolute;
}

.burger-menu__burger {
  opacity: 1;
  background-image: url("burger.2402b75d.svg");
  background-position: center;
  width: 24px;
  height: 24px;
  position: absolute;
}

